import React, { Component, useRef, useEffect } from 'react';
import style from './style.module.css';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import StarIcon from '@material-ui/icons/Star';
import Icon from '@material-ui/core/Icon';
import Box from '@material-ui/core/Box';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import { purple } from '@material-ui/core/colors';

const theme = {
    spacing: 8,
}

const newTheme = createMuiTheme({
    palette: {
      primary: {
        // Purple and green play nicely together.
        main: '#0f4c81',
      },
      secondary: {
        // This is green.A700 as hex.
        main: '#EDF5E1',
      },
    },
  });



const useStyles = makeStyles((theme) => ({
    button: {
      margin: theme.spacing(1),
    },
  }));
  

const skills = ['JavaScript', 'React.js', 'Git', 'SQL', 'MongoDB','API','Node.js',' –û–û–ü','–ü–∞—Ç—Ç–µ—Ä–Ω—ã –ø—Ä–æ–µ–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è','CSS','Linux'];

const listItems = skills.map((skill) =>
    <div className ={style.itemWrapper}>
        <ThemeProvider theme={newTheme}>
            <Box p={1}>
                <Button
                variant="contained"
                color='secondary'
                startIcon={<StarIcon color='primary'/>}
                >
                {skill}
                </Button>
            </Box>
        </ThemeProvider>
    </div>
);

const SkillPull = (props) =>{

    //—Å—Å—ã–ª–∫–∞ –¥–ª—è —Å–∫—Ä–æ–ª–ª–∞ –ø–æ –∫–Ω–æ–ø–∫–µ
    const myRef = props.myRef;
    /////////////////////
    const classes = useStyles();

    return(
        <div className = {style.wrapper}>
            {listItems}
        </div>
    );
}

export default SkillPull;