import React,{ Component, useRef, useEffect } from 'react';
import style from './style.module.css'
import Button from '@material-ui/core/Button';
import TelegramIcon from '@material-ui/icons/Telegram';
import StarIcon from '@material-ui/icons/Star';
import GitHubIcon from '@material-ui/icons/GitHub';
import AlternateEmailIcon from '@material-ui/icons/AlternateEmail';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import WorkIcon from '@material-ui/icons/Work';
import { spacing } from '@material-ui/system';
import { makeStyles } from '@material-ui/core/styles';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import LinkHolder from '../LinkHolder/LinkHolder';
import Pdf from './resume.pdf';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    '& > *': {
      margin: theme.spacing(1),
    },
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
  },
}));



const scrollToSkills =()=>{
  const windowHeigth = window.innerHeight;
  window.scrollBy(0,windowHeigth);
}


const RefButtons = () =>{

  const [open, setOpen] = React.useState(false);
  const handleClose = () => {
    setOpen(false);
  };
  const handleToggle = () => {
    setOpen(!open);
  };

  const classes = useStyles();
  let link = '#';

const SimpleBackdrop = (props) =>{
  return (
    <div>
      <Backdrop className={classes.backdrop} open={props.open} onClick={props.handleClose}>
        <LinkHolder/>
      </Backdrop>
    </div>
  );
}

  return (
    <div className = {style.wrapper}>




      <div className={classes.root}>

      <ButtonGroup size="large" color="primary" aria-label="large outlined primary button group">
      <Button 
        onClick = {scrollToSkills}
        href = "#skills"
        variant="outlined" 
        color="primary"
        startIcon={<StarIcon />}
        style ={{textTransform: 'none'}}

        >
            <h2>–ù–∞–≤—ã–∫–∏</h2>
        </Button>

        <Button 
        variant="outlined" 
        color="primary"
        startIcon={<TelegramIcon />}
        style ={{textTransform: 'none'}}

        >
            <a href = 'https://t.me/Zhihorka'  className = {style.link}><h2>–¢–µ–ª–µ–≥—Ä–∞–º</h2></a>
        </Button>

      <Button 
      variant="outlined" 
      color="primary"
      startIcon={<GitHubIcon />}
      style ={{textTransform: 'none'}}

      >
          <a href = 'https://github.com/Zhihorka'  className = {style.link}><h2>GitHub</h2></a>
      </Button>
      <Button 
      variant="outlined" 
      color="primary"
      startIcon={<AlternateEmailIcon/>}
      style ={{textTransform: 'none'}}
      onClick = {handleToggle}
      >
          <h2>–ü–æ—á—Ç–∞</h2>
      </Button>
      <Button 
      variant="outlined" 
      color="primary"
      startIcon={<WorkIcon/>}
      style ={{textTransform: 'none'}}
      >
           <a href = {Pdf} target = "_blank" className = {style.link}><h2 >–†–µ–∑—é–º–µ</h2></a>
      </Button>
      </ButtonGroup>
      <SimpleBackdrop className={classes.backdrop} open={open} handleClose={handleClose}/>
    </div>

    </div>
  );
}

export default RefButtons;